<template>
  <div class="yuanshen_6_div">
    <div class="back_lbt">
      <div class="back_src">
        <!-- ,{'z-index':i.z_index}, -->
        <div class="back_list" v-for="i in img_list" :key="i.id" :style="[{backgroundImage:`url(${i.src})`},{opacity:i.opacity}]">

        </div>
      </div>

    </div>
    <div class="lbt_left" @click="btnLeft" @mouseover="lbt_hover('img1')" @mouseout="lbt_hover('img1')">
      <img :src="img_isHover.img1 ? require('./image/btn_left_hover.webp') : require('./image/btn_left.webp')" alt="">
    </div>
    <div class="lbt_right" @click="btnright" @mouseover="lbt_hover('img2')" @mouseout="lbt_hover('img2')">
      <img :src="img_isHover.img2 ? require('./image/btn_right_hover.webp') : require('./image/btn_right.webp')" alt="">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      listImgId: '0',
      // 按钮hover效果
      img_isHover: {
        img1: false,
        img2: false
      },
      img_list: [
        { id: '0', src: require('./image/img1.webp'), z_index: 11, opacity: 1 },
        { id: '1', src: require('./image/img2.webp'), z_index: 0, opacity: 0 },
        { id: '2', src: require('./image/img3.webp'), z_index: 0, opacity: 0 },
        { id: '3', src: require('./image/img4.webp'), z_index: 0, opacity: 0 },
        { id: '4', src: require('./image/img5.webp'), z_index: 0, opacity: 0 }
      ]
    }
  },

  methods: {
    lbt_hover(value) {
      this.img_isHover[value] = !this.img_isHover[value]
    },
    // 左右按钮的点击事件
    btnLeft() {
      for (let i = 0; i < this.img_list.length; i++) {
        this.img_list[i].opacity = 0
        if (this.listImgId == this.img_list[i].id) {
          // 当图片是第一位时 执行此逻辑
          if (i == 0) {
            this.img_list[this.img_list.length - 1].opacity = 1
            this.listImgId = this.img_list[this.img_list.length - 1].id
            return
          }
          this.img_list[this.listImgId - 1].opacity = 1
          this.listImgId = this.img_list[this.listImgId - 1].id
          return
        }
      }
    },
    btnright() {
      for (let i = 0; i < this.img_list.length; i++) {
        this.img_list[i].opacity = 0
        if (this.listImgId == this.img_list[i].id) {
          // 当图片是最后一位时 执行此逻辑
          if (i == this.img_list.length - 1) {
            this.img_list[0].opacity = 1
            this.listImgId = this.img_list[0].id
            return
          }
          // this.listImaId 是字符串会导致字符串拼接 从而找不到opacity属性
          this.img_list[Number(this.listImgId) + 1].opacity = 1
          this.listImgId = this.img_list[Number(this.listImgId) + 1].id
          return
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
.yuanshen_6_div {
  width: 100%;
  height: 100%;
  position: relative;
  background: url('./image/back.webp') center center / cover no-repeat;
  .back_lbt {
    position: absolute;
    width: 16rem;
    height: 9.4rem;
    left: 4.35rem;
    top: 1.23rem;
    // overflow: hidden;
    .back_src {
      width: 5470px;
      position: relative;
      left: 0;
      top: 0;
      display: flex;
      margin-left: auto;
      margin-right: auto;
      height: 100%;
      .back_list {
        width: 1094px;
        height: 100%;
        position: absolute;
        left: 0px;
        top: 0px;
        transition: 0.3s;
        background: center center / cover no-repeat;
      }
    }
  }
  .lbt_left {
    cursor: pointer;
    position: absolute;
    width: 1.67rem;
    height: 1.6rem;
    left: 5.39rem;
    top: 5rem;
    z-index: 22;
    font-size: 75.6481px;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  .lbt_right {
    cursor: pointer;
    position: absolute;
    width: 1.67rem;
    height: 1.6rem;
    top: 5rem;
    right: 5.2rem;
    z-index: 33;
    font-size: 75.6481px;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
}
</style>