    item_img_over(id) {
      this.lists.forEach((value, index) => {
        if (this.lists[index].isHead_over) return
        if (this.lists[index].id == id) {
          // console.log('0000')
          this.lists[index].isHead = !this.lists[index].isHead
        }
      })
