        if (this.lists[index].index == this.transformIndex) {
          this.lists[index].isHead = false
          this.lists[index].isHead_over = true
          // 切换角色的固有图片
          this.back = { backgroundImage: `url(${this.lists[index].back})` }
          this.backImg.back_img1 = this.lists[index].item_language_Chinese
          this.backImg.back_img2 = this.lists[index].item_language_Japanese
          this.backImg.back_img1_hover = this.lists[index].item_language_Chinese_hover
