<template>
  <div class="yuanshen_8_div">
    <div class="back_lbt_">
      <div class="back_lbt_list">
        <!-- 渲染体 -->
        <div :class=" item.style" v-for="item in list_back" :key="item.id" :style="item.style">
          <div class="bacl_lbt_list_item" :style="[{backgroundImage:`url(${item.back})`}]"></div>

        </div>
      </div>
    </div>
    <div class="back_index">
      <div class="back_index_item" v-for="(item,index) in list_back_index" :key="item.id">
        <img :src="!item.isHover ? require('./image/index_btn.webp') : require('./image/index_btn_hover.webp')" @mouseover="back_index_hover(index)" @mouseout="back_index_hover(index)" @click="back_index_click(index)">
      </div>
    </div>
    <div class="btn_left" @click="btn_left"><img :src="btn_hover_obj.img1 ? require('./image/btn_left.webp') : require('./image/btn_left_hover.webp')" alt="" @mouseover="btn_hover('img1')" @mouseout="btn_hover('img1')"></div>
    <div class="btn_right" @click="btn_right"><img :src="btn_hover_obj.img2 ? require('./image/btn_right.webp') : require('./image/btn_right_hover.webp')" alt="" @mouseover="btn_hover('img2')" @mouseout="btn_hover('img2')"></div>
    <div class="grid_">
      <img src="./image/新浪微博.webp" alt="">
      <img src="./image/微信.webp" alt="">
      <img src="./image/哔哩哔哩.webp" alt="">
      <img src="./image/米游社.webp" alt="">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      btn_hover_obj: {
        img1: true,
        img2: true
      },
      index_top: 0, // 当前的list_style 索引值
      // index_botton: 0, // 上一次的list_style 索引值
      list_style: [
        { back_lbt_lists: true, item_style_1: true },
        { back_lbt_lists: true, item_style_2: true },
        { back_lbt_lists: true, item_style_3: true },
        { back_lbt_lists: true, item_style_4: true },
        { back_lbt_lists: true, item_style_5: true }
      ],
      // {
      //   style: [{ style1: ['back_lbt_lists', 'item_style_1'], style2: ['back_lbt_lists', 'item_style_2'], style3: ['back_lbt_lists', 'item_style_3'], style4: ['back_lbt_lists', 'item_style_4'], style5: ['back_lbt_lists', 'item_style_5'] }]
      // }
      list_back_index: [] // 轮播图索引值专用
    }
  },
  computed: {
    list_back() {
      return [
        {
          id: '1',
          back: require('./image/lbt_img1.webp'),
          isHover: true,
          isClick: true,
          // style: ['back_lbt_lists', 'item_style_1']
          // style: this.list_style.style[0].style1
          style: this.list_style[0]
        },
        {
          id: '2',
          back: require('./image/lbt_img2.webp'),
          isHover: false,
          isClick: false,
          // style: ['back_lbt_lists', 'item_style_2']
          // style: this.list_style.style[0].style2
          style: this.list_style[1]
        },
        {
          id: '3',
          back: require('./image/lbt_img3.webp'),
          isHover: false,
          isClick: false,
          // style: ['back_lbt_lists', 'item_style_3']
          // style: this.list_style.style[0].style3
          style: this.list_style[2]
        },
        {
          id: '4',
          back: require('./image/lbt_img4.webp'),
          isHover: false,
          isClick: false,
          // style: ['back_lbt_lists', 'item_style_4']
          // style: this.list_style.style[0].style4
          style: this.list_style[3]
        },
        {
          id: '5',
          back: require('./image/lbt_img5.webp'),
          isHover: false,
          isClick: false,
          // style: ['back_lbt_lists', 'item_style_5']
          // style: this.list_style.style[0].style5
          style: this.list_style[4]
        }
      ]
    }
  },
  methods: {
    // 减少重复代码
    upIndex() {
      this.list_back_index.forEach((value, i) => {
        this.list_back_index[i].isClick = false
        this.list_back_index[i].isHover = false
        // if (i == index) {
        if (i == this.index_top) {
          this.list_back_index[i].isClick = true
          this.list_back_index[i].isHover = true
        }
      })
    },
    btn_left() {
      if (this.index_top == 0) {
        this.index_top = 4
      } else {
        this.index_top -= 1
      }
      this.list_style.push(this.list_style.shift())
      /// 重新渲染索引
      // this.upIndex(this.index_top)
      this.upIndex()
    },
    btn_right() {
      if (this.index_top == 4) {
        this.index_top = 0
      } else {
        this.index_top += 1
      }
      this.list_style.unshift(this.list_style.pop())
      /// 重新渲染索引
      this.upIndex()
    },
    // 索引的触摸事件
    back_index_hover(index) {
      if (this.list_back_index[index].isClick) return
      this.list_back_index[index].isHover = !this.list_back_index[index].isHover
    },
    // 索引点击事件
    back_index_click(index) {
      if (this.list_back_index[index].isClick) return
      this.upIndex()

      // this.list_back_index.forEach((value, i) => {
      //   this.list_back_index[i].isClick = false
      //   this.list_back_index[i].isHover = false
      //   if (i == index) {
      //     this.list_back_index[i].isClick = true
      //     this.list_back_index[i].isHover = true
      //   }
      // })
      // 切换样式逻辑
      if (this.index_top < index) {
        let count = index - this.index_top
        for (let i = 0; i < count; i++) {
          this.btn_right()
        }
      }
      if (this.index_top > index) {
        let count = this.index_top - index
        for (let i = 0; i < count; i++) {
          this.btn_left()
        }
      }
    },
    btn_hover(value) {
      // console.log(!this.btn_hover_obj[value])
      this.btn_hover_obj[value] = !this.btn_hover_obj[value]
    }
  },
  mounted() {
    this.list_back_index = this.list_back
  }
}
</script>

<style scoped lang="less">
// 分页效果轮播图
.item_style_1 {
  transform: translateX(0px) scale(1);
  opacity: 1;
  z-index: 2;
}
.item_style_2 {
  transform: translateX(3.5em) scale(0.71);
  opacity: 1;
  z-index: 1;
  --ch-swiper-brightness: 0.51;
}
.item_style_3 {
  transform: translateX(3.5em) scale(0.42);
  opacity: 0;
  --ch-swiper-brightness: 0.51;
}
.item_style_4 {
  transform: translateX(-3.5em) scale(0.42);
  opacity: 0;
  --ch-swiper-brightness: 0.51;
}
.item_style_5 {
  transform: translateX(-3.5em) scale(0.71);
  opacity: 1;
  z-index: 1;
  --ch-swiper-brightness: 0.51;
}
.yuanshen_8_div {
  position: relative;
  width: 100%;
  height: 100%;
  background: url('./image/back.webp') center center / cover no-repeat;
  // 轮播图
  .back_lbt_ {
    position: absolute;
    width: 15.56rem;
    height: 6.28rem;
    left: 4.8rem;
    top: 1.1rem;
    font-size: 75.6481px;
    .back_lbt_list {
      position: relative;
      width: inherit;
      height: inherit;
      z-index: 0;
      overflow: hidden;
      .back_lbt_lists {
        height: 5.65em;
        width: 9.49em;
        left: calc(50% - 4.745em);
        transition: 0.3s;
        filter: brightness(var(--ch-swiper-brightness)); // 蒙尘效果来源
        // transform: translateX(0px) scale(1);
        left: calc(50% - 4.745em);
        // --ch-swiper-brightness: 0.51;
        // transform: translateX(-3.5em) scale(0.71);
        // opacity: 1;
        // z-index: 1;
        position: absolute;
        top: 0;
        // opacity: 0;
        .bacl_lbt_list_item {
          background: center center / cover no-repeat;
          overflow: visible;
          position: relative;
          width: 100%;
          height: 100%;
          display: inline-block;
        }
      }
    }
  }
  // 左按钮
  .btn_left {
    cursor: pointer;
    position: absolute;
    width: 1.6rem;
    height: 1.58rem;
    left: 5.9rem;
    top: 3.59rem;
    font-size: 75.6481px;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  // 右按钮
  .btn_right {
    cursor: pointer;
    position: absolute;
    width: 1.6rem;
    height: 1.58rem;
    top: 3.59rem;
    right: 5.7rem;
    font-size: 75.6481px;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  .back_index {
    background: center center / 9.06rem 0.01rem no-repeat;
    overflow: hidden;
    position: absolute;
    width: 4.1rem;
    height: 0.64rem;
    left: 50%;
    top: 7.72rem;
    transform: translate(-50%, 0px);
    z-index: 1;
    display: flex;
    justify-content: space-between;

    .back_index_item {
      cursor: pointer;
      position: relative;
      width: 0.64rem;
      height: 0.64rem;
      margin-left: 0rem;
      font-size: 75.6481px;
      img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
      }
    }
  }
  // 广告样式
  .grid_ {
    background: center center / cover no-repeat;
    overflow: hidden;
    position: absolute;
    width: 25rem;
    height: 1.7rem;
    left: 0rem;
    top: 9.64rem;
    display: flex;
    img {
      display: inline-block;
      background: center/cover no-repeat;
      vertical-align: top;
      line-height: 1.15;
    }
    img:nth-child(1) {
      cursor: pointer;
      position: absolute;
      width: 1.2rem;
      height: 1.5rem;
      left: 8.4rem;
      top: 0rem;
      font-size: 75.6481px;
    }
    img:nth-child(2) {
      cursor: pointer;
      position: absolute;
      width: 1.3rem;
      height: 1.5rem;
      left: 10.55rem;
      top: 0rem;
      font-size: 75.6481px;
    }
    img:nth-child(3) {
      cursor: pointer;
      position: absolute;
      width: 1.71rem;
      height: 1.5rem;
      left: 12.75rem;
      top: 0rem;
      font-size: 75.6481px;
    }
    img:nth-child(4) {
      cursor: pointer;
      position: absolute;
      width: 1.2rem;
      height: 1.55rem;
      left: 15.29rem;
      top: 0rem;
      font-size: 75.6481px;
    }
  }
}
</style>