<template>
  <div class="yuanshen">
    <div class="yuanshen_3_div">
      <div class="div_img1" @click="divClick1">
        <img src="./image/home_img1.webp" alt="" @mouseover="div_over($event,'img1')" @mouseout="div_out($event,'img1')">
      </div>
      <div class="div_img2" alt="">
        <img src="./image/home_img2.webp" alt="" @mouseover="div_over($event,'img2')" @mouseout="div_out($event,'img2')">
      </div>
      <div class="div_img3" alt="">
        <img src="./image/home_img3.webp" alt="" @mouseover="div_over($event,'img3')" @mouseout="div_out($event,'img3')">
      </div>
      <div class="div_img4">
        <img src="./image/home_img4.webp" alt="" @mouseover="div_over($event,'img4')" @mouseout="div_out($event,'img4')">
      </div>
    </div>

    <div class="video" v-if="isVideo">
      <div class="video_" @click="videos">
        <div class="video_back">

        </div>
      </div>
      <div class="video_div video_divS" @click="videoss">

      </div>

      <div class="video_div">
        <video src="./image/0d8752c099781bd91f61f4e89f978a06_3037744960914858233.mp4" ref="videoAotuPlay"></video>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      images: {
        img1: [require('./image/home_img1.webp'), require('./image/home_img1_hover.webp')],
        img2: [require('./image/home_img2.webp'), require('./image/home_img2_hover.webp')],
        img3: [require('./image/home_img3.webp'), require('./image/home_img3_hover.webp')],
        img4: [require('./image/home_img4.webp'), require('./image/home_img4_hover.webp')]
      },
      isVideo: false
    }
  },
  methods: {
    div_over(e, src) {
      e.target.src = this.images[src][1]
    },
    div_out(e, src) {
      e.target.src = this.images[src][0]
    },
    divClick1() {
      this.$emit('isUpda', false)
      this.isVideo = true
      this.$nextTick(() => {
        this.$refs.videoAotuPlay.play()
      })
    },
    videos() {
      this.$emit('isUpda', true)
      this.isVideo = false
      this.$refs.videoAotuPlay.pause()
      // console.log('1')
    },
    videoss() {
      console.log('2')
    }
  }
}
</script>

