        img2: require('./image/qt_xiazai.webp'),
        img3: require('./image/yys_xiazai.webp')
      },
      classList: {
        yuanshen_home_div: ['yuan_home_div', 'yuan_home_div_image_1']
      },
      windows: {
        width: 0,
        heigth: 0
      }
    }
  },
  methods: {
    yuanshen_home_div_over() {
      this.classList.yuanshen_home_div = ['yuan_home_div', 'yuan_home_div_image_2']
    },
    yuanshen_home_div_out() {
      this.classList.yuanshen_home_div = ['yuan_home_div', 'yuan_home_div_image_1']
    },
    yuanshen_home_div_click() {
      window.open('https://ys.mihoyo.com/main/')
    },
    yuan_btn_bf_over() {
      this.yuan_btn_bf_no_url = yuan_btn_bf
    },
    yuan_btn_bf_out() {
      this.yuan_btn_bf_no_url = yuan_btn_bf_no
    },

    img_src(value, src) {
      this.imageList[value] = src
      // console.log(value, src)
    }
  },
  mounted() {
    this.windows.width = window.innerWidth
    this.windows.heigth = window.innerHeight
    // console.log(this.windows.width)
  }
}
