<template>
  <div class="yuanshen_7_div">
    <img class="img_xian" src="./image/xian.webp" alt="">
    <div class="home_back">
      <div class="home_back_for" v-for="i in list_item" :key="i.id" :style="[{backgroundImage:`url(${i.back})`},{opacity:i.opacity}]">

      </div>
    </div>
    <div class="item_img">
      <div class="item_img_div" v-for="(i,index) in list_item" :key="i.id" @click="click(index)" @mouseover="over(index)" @mouseout="over(index)">
        <img :src="i.isHover ? i.item_img_hover : i.item_img" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // back_src: require('./image/back_img1.webp'),
      list_item: [
        { id: '1', back: require('./image/back_img1.webp'), opacity: 1, item_img: require('./image/item_img1.webp'), item_img_hover: require('./image/item_img1_hover.webp'), isHover: true, isClick: true },
        { id: '2', back: require('./image/back_img2.webp'), opacity: 0, item_img: require('./image/item_img2.webp'), item_img_hover: require('./image/item_img2_hover.webp'), isHover: false, isClick: false },
        { id: '3', back: require('./image/back_img3.webp'), opacity: 0, item_img: require('./image/item_img3.webp'), item_img_hover: require('./image/item_img3_hover.webp'), isHover: false, isClick: false },
        { id: '4', back: require('./image/back_img4.webp'), opacity: 0, item_img: require('./image/item_img4.webp'), item_img_hover: require('./image/item_img4_hover.webp'), isHover: false, isClick: false }
      ]
    }
  },
  methods: {
    // 每一项的item鼠标点击事件
    click(index) {
      for (let i = 0; i < this.list_item.length; i++) {
        this.list_item[i].opacity = 0
        this.list_item[i].isHover = false
        this.list_item[i].isClick = false
        if (this.list_item[i].isClick) return // 此处要用索引值i 不能使用点击传来的value  以为value是不变的i是在变化  比如当前项高亮为索引值3 点击value为1 那么第二次循环的时候就直接return出去了 则item1和item3都处于高亮状态
        if (index == i) {
          this.list_item[index].isHover = true
          this.list_item[index].isClick = true
          // this.back_src = this.list_item[index].back // 方案一： 变换当前item对应的背景图  页面会闪烁的Bug

          this.list_item[index].opacity = 1 // 方案二：调整每一个属性的opacity值 以为图片全部都是缓存好的不会造成反应不及时问题
        }
      }
    },
    // 每一项的item鼠标移入移出事件
    over(index) {
      for (let i = 0; i < this.list_item.length; i++) {
        if (this.list_item[index].isClick) return
        if (i == index) {
          this.list_item[i].isHover = !this.list_item[i].isHover
          return
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
.yuanshen_7_div {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  background: url('./image/back.webp') center center / cover no-repeat;
  .img_xian {
    position: absolute;
    width: 0.03rem;
    height: 7.54rem;
    left: 18.43rem;
    top: 0.03rem;
    z-index: 2;
  }
  .home_back {
    position: absolute;
    width: 17.2rem;
    height: 12rem;
    left: 4.4rem;
    top: 0rem;
    background: center center / cover no-repeat;
    z-index: 1;
    .home_back_for {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: center center / cover no-repeat;
    }
  }
  .item_img {
    z-index: 3;
    cursor: pointer;
    position: absolute;
    // width: 1.68rem;
    // height: 1.67rem;
    top: 1.28rem;
    left: 17.5rem;
    .item_img_div {
      width: 1.9rem;
      height: 1.9rem;
      margin-bottom: 5px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>