<template>
  <div class="yuanshen_5_div">
    <div class="centre_">
      <div class="centre_back" :style="back_img"></div>
    </div>
    <div class="home_img1" @mouseover="home_img('img1')" @mouseout="home_img('img1')" @click="home_img_click('img1')">
      <img :src="img_obj.img1.is_hover ? require('./image/神里头像_hover.webp') : require('./image/神里头像.webp')" alt="">
    </div>
    <div class="home_img2" @mouseover="home_img('img2')" @mouseout="home_img('img2')" @click="home_img_click('img2')">
      <img :src="img_obj.img2.is_hover ? require('./image/丽莎头像_hover.webp') : require('./image/丽莎头像.webp')" alt="">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isback_img: true,
      // 控制图片的切换和展示
      img_obj: {
        img1: {
          is_hover: true, // 鼠标悬浮上面 是否展示hover
          is_click: true // 是否固定展示
        },
        img2: {
          is_hover: false,
          is_click: false
        }
      }
    }
  },
  methods: {
    home_img(value) {
      if (this.img_obj[value].is_click) return
      this.img_obj[value].is_hover = !this.img_obj[value].is_hover
    },
    home_img_click(value) {
      if (this.img_obj[value].is_click) return

      for (const key in this.img_obj) {
        this.img_obj[key].is_click = false
        this.img_obj[key].is_hover = false
        if (key == value) {
          this.isback_img = !this.isback_img // 背景取反转换
          this.img_obj[value].is_click = true
          this.img_obj[value].is_hover = true
        }
      }
    }
  },
  computed: {
    back_img() {
      return this.isback_img ? { backgroundImage: `url(${require('./image/神里皮肤.webp')})` } : { backgroundImage: `url(${require('./image/丽莎皮肤.webp')})` }
    }
  }
}
</script>

<style scoped lang="less">
.yuanshen_5_div {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: url('./image/home_back.webp') center center / cover no-repeat;
  .centre_ {
    position: absolute;
    width: 17rem;
    height: 12rem;
    left: 5.9rem;
    top: 0rem;
    .centre_back {
      transition: 0.3s;
      position: absolute;
      top: 0;
      left: 0;
      width: 1113px;
      height: 100%;
      background: center center / cover no-repeat;
    }
  }
  .home_img1 {
    cursor: pointer;
    position: absolute;
    width: 2rem;
    height: 2.1rem;
    left: 5.7rem;
    top: 2.8rem;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .home_img2 {
    cursor: pointer;
    position: absolute;
    width: 2rem;
    height: 2.1rem;
    left: 5.31rem;
    top: 5.2rem;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>