<template>
  <div class="div" @mousewheel="wheel" ref="div" :style="`transform:translate3d(0px, ${nub}px, 0px);`">
    <div class="div1">
      <YuanShen_1></YuanShen_1>
    </div>
    <div class="div2">
      <YuanShen_2></YuanShen_2>
    </div>
    <div class="div3">
      <YuanShen_3 ref="isupda"></YuanShen_3>
    </div>
    <div class="div4">
      <YuanShen_4 :isupda="isupda"></YuanShen_4>
    </div>
    <div class="div5">
      <YuanShen_5></YuanShen_5>
    </div>
    <div class="div6">
      <YuanShen_6></YuanShen_6>
    </div>
    <div class="div7">
      <YuanShen_7></YuanShen_7>
    </div>
    <div class="div8">
      <YuanShen_8></YuanShen_8>
    </div>
    <div class="div_botton">
      <YuanShen_bottom></YuanShen_bottom>
    </div>
  </div>
</template>

<script>
import YuanShen_1 from './yuanshen_1/index.vue'
import YuanShen_2 from './yuanshen_2/index.vue'
import YuanShen_3 from './yuanshen_3/index.vue'
import YuanShen_4 from './yuanshen_4/index.vue'
import YuanShen_5 from './yuanshen_5/index.vue'
import YuanShen_6 from './yuanshen_6/index.vue'
import YuanShen_7 from './yuanshen_7/index.vue'
import YuanShen_8 from './yuanshen_8/index.vue'
import YuanShen_bottom from './yuanshen_bottom/index.vue'
export default {
  components: { YuanShen_1, YuanShen_2, YuanShen_3, YuanShen_4, YuanShen_5, YuanShen_6, YuanShen_7, YuanShen_8, YuanShen_bottom },
  data() {
    return {
      time: null, // 节流变量
      index: 0,
      isMousewheel: true
    }
  },
  computed: {
    nub: {
      get() {
        // 最后一页的逻辑判断
        if (this.index == -8) {
          return -7 * window.innerHeight - 343
        }
        return this.index * window.innerHeight // 以浏览器视口进行切换高度
      }
    }
  },
  methods: {
    isupda(value) {
      this.isMousewheel = value
    },
    wheel(event) {
      event.preventDefault() // 禁止页面滑动     event.preventDefault() // 禁止页面滑动
      if (!this.isMousewheel) return
      // console.log(event.deltaY) // 滚轮事件 负数为下滑 正数为上滑

      console.log()
      if (this.time) return
      this.time = setTimeout(() => {
        if (event.deltaY > 0) {
          if (this.index == -8) return (this.time = null) // 判断是否是最大页数
          this.index -= 1
          sessionStorage.setItem('yuanshen_home_index', this.index)

          // console.log('下')
        } else {
          if (this.index == 0) return (this.time = null)
          this.index += 1
          sessionStorage.setItem('yuanshen_home_index', this.index)

          // console.log('上')
        }
        this.time = null
      }, 250)
    }
  },
  mounted() {
    if (sessionStorage.getItem('yuanshen_home_index')) {
      this.index = Number(sessionStorage.getItem('yuanshen_home_index'))
    } else {
      sessionStorage.setItem('yuanshen_home_index', this.index)
    }
    this.$refs.isupda.$on('isUpda', value => {
      // console.log(value)
      this.isMousewheel = value
    })
  }
}
</script>

<style scoped>
.div {
  width: 100%;
  height: 100%;
  transition: 0.4s;
}

.div1 {
  width: 100%;
  height: 100%;
}

.div2 {
  width: 100%;
  height: 100%;
}

.div3 {
  width: 100%;
  height: 100%;
}

.div4 {
  width: 100%;
  height: 100%;
}
.div5 {
  width: 100%;
  height: 100%;
}
.div6 {
  width: 100%;
  height: 100%;
}
.div7 {
  width: 100%;
  height: 100%;
}
.div8 {
  width: 100%;
  height: 100%;
}
.div_botton {
  width: 100%;
  /* height: auto; */
  height: 343px;
}
</style>